import './App.scss';
import {Switch, Route} from 'react-router-dom'
import Home from './Component/Page/Home/Home';
import Profile from './Component/Page/profile/Profile';
import bolimlararo from './Component/Page/bolimlar/bolimlar';
import Videos from './Component/Page/videos/videos';
function App() {
  return (
    <>
      <Switch>
        <Route path="/" component={Home} exact />
        <Route path="/shunchaki/:bolim" component={bolimlararo} exact />
        <Route path="/profile/:id" component={Profile} exact />
        <Route path="/videos/:url" component={Videos} exact />
      </Switch>

    </>
  );
}

export default App;
